<!--  -->
<template>
<div class='shopDetails'>
    <div class='nav'>
        <span :class="['nav-list',nav_index==index?'active':'']" v-for='(item,index) in nav' :key='index'  @click='navTab(index)'>{{item}}</span>
    </div>
    <nut-swiper direction="horizontal" :loop="true" :canDragging="false" :paginationVisible="true" :autoPlay="5000" :speed='1000' class='shop-swiper'  ref="swipers">
        <div  class="nut-swiper-slide "  v-for='(item,index) in banner' :key='index'>
            <img :src="item" alt="" class='banner-img' >
        </div>
    </nut-swiper>
    <div class='shopInfo'>
        <p class='shopName'>{{info.title}}</p>
        <div class='shopMoney'>
            <p class='moneyBox'>
                <span class='money-now'>￥{{info.sellPrice}}</span>
                <span class='money-old'>￥{{info.marketPrice}}</span>
            </p>
            <span class='shop-xiao'>销量：{{info.salesVolume}}</span>
        </div>
    </div>
    <div class='cont'>
        <img :src="item" alt="" v-for='(item,index) in info.thumbPicture' :key='index' class='imgList' >
    </div>
    <div class='btm'>
        <div class='btm-list'>
            <img src="../../../static/image/share.png" alt="" class='share-icon' >
            <span class='btm-text'>分享</span>
        </div>
        <div class='btm-list'>
            <router-link to='/car'>
                <img src="../../../static/image/carss.png" alt="" class='share-icon' >
                <span class='btm-text'>购物车</span>
            </router-link>
        </div>
        <div class='btm-list'>
            <img src="../../../static/image/collects.png" alt="" class='share-icon' >
            <span class='btm-text'>客服</span>
        </div>
        <div class='addCar' @click="addCar" >加入购物车</div>
        <div class='buy'>立即购买</div>
    </div>
</div>
</template>

<script>
    import {shopDetails} from '../../api/api';
    import { mapActions } from 'vuex'
    export default {
    //import引入的组件需要注入到对象中才能使用
    components: {},
    data() {
    //这里存放数据
    return {
        banner:[],
        info:'',
        nav:['商品','详情'],
        nav_index:0
    };
    },
    //监听属性 类似于data概念
    computed: {},
    //监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        ...mapActions(['headTitle','showBtm','goBack','addGoods']),
        navTab:function(index){
            this.nav_index=index
            if(index==0){
               document.documentElement.scrollTop=0;
            }else{
                document.documentElement.scrollTop=500;
            }
        },
        // 加入购物车
        addCar:function(){
            this.addGoods(this.info)
        }
    },
    //生命周期 - 创建完成（可以访问当前this实例）
    created() {
        this.headTitle('详情展示')
        this.showBtm(false)
        this.goBack(true)
    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {
        this.loading = this.$toast.loading();
        shopDetails(this.$route.params.id).then(response=>{
            this.loading.hide();
            this.banner=response.data.data.pictures
            this.info=response.data.data
            this.$refs.swipers.updateEvent()
        });
    },
    beforeCreate() {}, //生命周期 - 创建之前
    beforeMount() {}, //生命周期 - 挂载之前
    beforeUpdate() {}, //生命周期 - 更新之前
    updated() {}, //生命周期 - 更新之后
    beforeDestroy() {}, //生命周期 - 销毁之前
    destroyed() {}, //生命周期 - 销毁完成
    activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
    }
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
.nav{
    width:100%;
    height:88px;
    display:flex;
    justify-content:space-around;
    position: fixed;
    left:0;
    top:90px;
    background:#fff;
    z-index:1;
}
.nav-list{
    display:inline-block;
    font-size:28px;
    color:rgb(109,109,109);
    line-height:88px;
}
.nav-list.active{
    font-size:30px;
    color:#000;
    border-bottom:5px solid rgb(232,62,36);
}
.shopDetails{
    padding-bottom:120px;
    padding-top:178px;
}
.shop-swiper{
    width:100%;
    height:750px;
    background:#fff;
}
.banner-img{
    display:block;
    width:100%;
    height:750px;
}
.shopInfo{
    background:#fff;
    padding:30px;
    margin-bottom:20px;
}
.shopName{
    font-size:34px;
    color:#000;
    font-weight:bold;
    margin-bottom:20px;
    text-align: left;
}
.shopMoney{
    width:100%;
    display:flex;
    align-items:flex-end;
    justify-content:space-between;
}
.moneyBox{
    display:flex;
    align-items:flex-end;
}
.money-now{
    display:block;
    font-size:40px;
    font-weight:bold;
    color:rgb(232,62,36);
}
.money-old{
    display:block;
    font-size:24px;
    color:rgb(41,41,41);
    margin-left:16px;
    text-decoration:line-through;
}
.shop-xiao{
    display:block;
    font-size:24px;
    color:rgb(41,41,41);
    margin-left:16px;
}
.imgList{
    width:100%;
    display:block;
}
.btm{
    width:100%;
    height:98px;
    background:#fff;
    position:fixed;
    left:0;
    bottom:0;
    box-shadow: 0px -10px 14px 0 rgba(0,0,0,0.2);
    display:flex;
    justify-content:space-around;
    align-items:center;
}
.share-icon{
    display:block;
    width:36px;
    height:36px;
    margin:0 auto;
}
.btm-text{
    font-size:24px;
    color:#2b2b2b;
    display:block;
    text-align: center;
}
.addCar{
    width:200px;
    height:60px;
    border-radius:50px;
    border:2px solid rgb(232,62,36);
    text-align:center;
    line-height:60px;
    color:rgb(232,62,36);
    font-size:30px;
}
.buy{
    width:200px;
    height:60px;
    border-radius:50px;
    background: rgb(232,62,36);
    color:#fff;
    text-align:center;
    line-height:60px;
    font-size:30px;
}
</style>


